<template>
	<view class="login_box">
		<view class="textarea">
			<input @input="aa" class="input-account" placeholder="请输入邮箱" focus />
			<view :class="judge_type"><text>{{detection}}</text></view>
			<view class="code-view">
				<input @input="minitor_code" class="input-code" id="a" placeholder="输入验证码" />
				<text @click="getVerifyCode">{{verify_code}}</text>
			</view>
			<button @click="inputpwd" :disabled="button_judge">下一步</button>
		</view>

		<!-- <input class="uni-input-input" placeholder="test"/> -->
	</view>
</template>

<script>
	export default {
		data() {
			return {
				verify_judge:false,
				button_judge: true,
				judge_type: 'email_right',
				detection: null,
				format_judge: false,
				email_user_judge: null,
				verify_code:'获 取 验 证 码',
				count_down:5,
				email:null,
				verify_input_judge:false
			}
		},
		/* uni-app常用到的几个提示框
		https://yanyan.blog.csdn.net/article/details/108950967?utm_medium=distribute.pc_relevant_t0.none-task-blog-BlogCommendFromMachineLearnPai2-1.control&depth_1-utm_source=distribute.pc_relevant_t0.none-task-blog-BlogCommendFromMachineLearnPai2-1.control
		 */
		
		/* uni-app获取input框的value中（@input）
		https://blog.csdn.net/Xl4277/article/details/97753121?utm_medium=distribute.pc_relevant.none-task-blog-BlogCommendFromMachineLearnPai2-1.control&depth_1-utm_source=distribute.pc_relevant.none-task-blog-BlogCommendFromMachineLearnPai2-1.control
		 */
		
		/* uniApp正则表达式校验手机、邮箱
		https://blog.csdn.net/qq_32958797/article/details/89032432
		 */
		methods: {
			// 判断邮箱格式
			aa(e) {

				const format_judge = RegExp(/^\w{3,}(.\w+)*@[A-z0-9]+(.[A-z]{2,5}){1,2}$/).test(e.detail.value)
				if (!format_judge) {
					this.detection = '邮箱格式错误'
					this.judge_type = 'email_wrong'
					console.log(this.judge_type)
					this.format_judge = false
				} else {
					this.detection = '邮箱格式正确'
					this.judge_type = 'email_right'
					this.format_judge = true
					this.email=e.detail.value
					//const used_judge= this.verify(e.detail.value)
					/* uni.showLoading({
						title: "校验中"
					}) */
					// const used_judge = true
					/* setTimeout(() => {
						if (!used_judge) {

							this.judge_type = 'email_wrong'
							this.detection = '该邮箱已被使用'
							this.verify_judge = false;
						} else {
							this.detection = '该邮箱可以使用'
							this.judge_type = 'email_right'
							// this.button_judge = false
							this.verify_judge = true;
						}
						uni.hideLoading()
					}, 2000) */
				}
			},
			// 检验邮箱是否被使用
			async verify(email) {
				const result = await this.$myRequest({
					url: '' + email
				})
				uni.showLoading({
					title: '校验中'
				})
				return result;
			},
			// 跳转导航
			inputpwd() {
				uni.navigateTo({
					url: '/pages/register/register-pwd/register-pwd'
				})
			},
			//获取验证码相关
			getVerifyCode(){
				if(this.format_judge){
					//验证邮箱是否可用
					console.log("邮箱："+this.email)
					//const used_judge= this.verify(e.detail.value)
					const used_judge=true
						if (!used_judge) {
							uni.showToast({
								title:'该邮箱已被注册',
								icon:'none',
								duration:2000
							})
							
						} else {
							uni.showToast({
								title:'已发送邮件',
								icon:'none',
								duration:2000
							})
							this.verify_code='重新获取'+' ('+this.count_down+')'
							
							this.format_judge=false
							this.verify_input_judge=true
							this.reGetCode()
							
						}
						uni.hideLoading()
				}
			},
			
			//定时器
			reGetCode(){
				const timer = setInterval(()=>{
					this.count_down=this.count_down-1
					this.verify_code='重新获取'+' ('+this.count_down+')'
					if(this.count_down===0){
						clearInterval(timer)
						this.format_judge=true
						this.count_down=10
					}
				},1000)
			},
			// 监听输入的验证码
			minitor_code(e){
				if((e.detail.value).length===6&&this.verify_input_judge===true){
					
					setTimeout(()=>{
						alert("验证验证码")
						this.button_judge=false
						},2000)
				}else{
					//alert("验证验错误")
				}
			}
			
		}
	}
</script>

<style lang="scss">
	.email_right {
		color: #84ED7C;
		// background-color: #84ED7C;
	}

	.email_wrong {
		color: red;
		// background-color: red;
	}

	.textarea {
		position: relative;
		width: 80%;
		margin: 150rpx auto;

		.code-view {
			width: 100%;
			display: flex;
			height: 150rpx;
			justify-content: space-between;
			flex-direction: row;
			align-items: center;
			.input-code {
				width: 60%;
				border: 1px solid black;
				border-radius: 10rpx;
				height: 80rpx;
				padding: 5rpx 15rpx;
			}
			text{
				font-size: 32rpx;
				line-height: 150rpx;
				color: $uni-color-blue;
			}
		}

		.input-account {
			border: 1px solid black;
			height: 80rpx;
			margin: 60rpx auto 0rpx;
			border-radius: 10rpx;
			padding: 5rpx 15rpx;
		}

		text {
			
		}

		view {
			display: flex;
			flex-direction: row;
			justify-content: flex-start;
		}

		button {
			margin-top: 40rpx;
			border: 1px solid black;

		}
	}
</style>
